import {
  IonContent,
  IonHeader,
  IonLabel,
  IonList,
  IonPage,
  IonToolbar,
  IonImg,
  IonItem,
  IonSlides,
  IonSlide,
  IonRange,
  IonIcon,
  IonAlert
} from '@ionic/react';
import React, { useEffect, useState, createRef, useRef, useCallback } from 'react';
import './Dashboard.scss';

import logoSvg from '../../assets/logo.svg';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../store';
import { AppDispatch } from '../../store/store';
import { updateCurrentProfile, updateTemperature, setTemperatures, lock, unlock, } from '../../store/reducers/bleReducer';
import { thermometer } from 'ionicons/icons';
import { RangeChangeEventDetail } from '@ionic/core';

const slideLabels = ['Low', 'Medium', 'High', 'F1', 'F2', 'Concentrates'];

const version = require('../../../package.json').version;

interface IDashboardProps {
  readonly displayLockDialog: boolean;
  readonly dismissLockDialogCallback: () => void;
}

const Dashboard: React.FC<IDashboardProps> = (props: IDashboardProps) => {
  const temperatures = useSelector((state: RootState) => state.ble.temperatures);
  const profile = useSelector((state: RootState) => state.ble.profile);
  const updating = useSelector((state: RootState) => state.ble.updating);
  const battery = useSelector((state: RootState) => state.ble.battery);
  const locked = useSelector((state: RootState) => (state.ble.status & 1) === 1);

  const dispatch = useDispatch<AppDispatch>();

  const [currentProfile, setCurrentProfile] = useState(-1);
  const [tempDialogOpen, setTempDialogOpen] = useState(false);
  //const [initialized, setInitialized] = useState(false);

  const slider = createRef<any>();
  const currentTemp = useRef(0);

  const updateTemperatureDispatch = useCallback((value: number) => {
    const newTemp = [
      ...temperatures.slice(0, profile - 1),
      value,
      ...temperatures.slice(profile)
    ];
    dispatch(updateTemperature(newTemp, profile));
  }, [temperatures, profile, dispatch]);

  useEffect(() => {
    if (slider.current) {
      slider.current.lockSwipes(updating);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [updating]);

  useEffect(() => {
    if (slider && slider.current && (profile > 0)) {
      try {
        if (slider.current) {
          slider.current.getActiveIndex().then((index: number) => {
            if ((profile !== (index + 1)) && (profile > 0) && slider.current) {
              slider.current.slideTo(profile - 1);
            }
          });
        }
      } catch (e) {
        console.log(e);
      }
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [profile]);

  const slideChanged = async (ev: CustomEvent) => {
    if (!slider.current) {
      return;
    }

    if (currentProfile === profile) {
      const index = await slider.current.getActiveIndex();
      if (index !== (profile - 1)) {
        dispatch(updateCurrentProfile(index + 1));
        setCurrentProfile(index + 1);
      }
    } else if (currentProfile !== profile) {
      setCurrentProfile(profile);
    }
  };

  const tempChange = (ev: CustomEvent<RangeChangeEventDetail>) => {
    const value = ev.detail.value as number;
    const newTemp = [
      ...temperatures.slice(0, profile - 1),
      value,
      ...temperatures.slice(profile)
    ];
    dispatch(setTemperatures(newTemp));
    setTimeout(() => {
      if (currentTemp.current === value) {
        dispatch(updateTemperature(newTemp, profile));
      }
    }, 1000);
    currentTemp.current = value;
  };

  const getBatteryIcon = () => {
    //    const batteryLevel = Math.round(battery / 25);
    return <svg width={42} height={18} className="battery" key={`batteryIndicator_${battery}`}>
      <rect width={34} height={14}
        stroke="#CCC" strokeWidth={2}
        x={2} y={2} rx={2} ry={2}
        className="battery-container" />
      <rect
        height={12}
        width={Math.round(battery / 100 * 32)}
        x={3} y={3} rx={2} ry={2}
        className="battery-gauge" />
      <rect width={4} height={8} x={36} y={6} className="battery-button" />
      {/* <text x={4} y={13} className="battery-text">
        {battery.toFixed(0)}%
      </text> */}
    </svg>;
  };

  const slideOpts = {
    allowSlideNext: !updating,
    allowSlidePrev: !updating,
    initialSlide: profile > 0 ? profile - 1 : 0,
    slidesPerView: 3,
    centeredSlides: true,
    coverflowEffect: {
      rotate: 50,
      stretch: 0,
      depth: 100,
      modifier: 1,
      slideShadows: true,
    },
    on: {
      beforeInit() {
        const swiper: any = this;

        swiper.classNames.push(`${swiper.params.containerModifierClass}coverflow`);
        swiper.classNames.push(`${swiper.params.containerModifierClass}3d`);

        swiper.params.watchSlidesProgress = true;
        swiper.originalParams.watchSlidesProgress = true;
      },
      setTranslate() {
        const swiper: any = this;
        const {
          width: swiperWidth, height: swiperHeight, slides, $wrapperEl, slidesSizesGrid
        } = swiper;
        const params = swiper.params.coverflowEffect;
        const isHorizontal = swiper.isHorizontal();
        const transform$$1 = swiper.translate;
        const center = isHorizontal ? -transform$$1 + (swiperWidth / 2) : -transform$$1 + (swiperHeight / 2);
        const rotate = isHorizontal ? params.rotate : -params.rotate;
        const translate = params.depth;
        // Each slide offset from center
        for (let i = 0, length = slides.length; i < length; i += 1) {
          const $slideEl = slides.eq(i);
          const slideSize = slidesSizesGrid[i];
          const slideOffset = $slideEl[0].swiperSlideOffset;
          const offsetMultiplier = ((center - slideOffset - (slideSize / 2)) / slideSize) * params.modifier;

          let rotateY = isHorizontal ? rotate * offsetMultiplier : 0;
          let rotateX = isHorizontal ? 0 : rotate * offsetMultiplier;
          // var rotateZ = 0
          let translateZ = -translate * Math.abs(offsetMultiplier);

          let translateY = isHorizontal ? 0 : params.stretch * (offsetMultiplier);
          let translateX = isHorizontal ? params.stretch * (offsetMultiplier) : 0;

          // Fix for ultra small values
          if (Math.abs(translateX) < 0.001) translateX = 0;
          if (Math.abs(translateY) < 0.001) translateY = 0;
          if (Math.abs(translateZ) < 0.001) translateZ = 0;
          if (Math.abs(rotateY) < 0.001) rotateY = 0;
          if (Math.abs(rotateX) < 0.001) rotateX = 0;

          const slideTransform = `translate3d(${translateX}px,${translateY}px,${translateZ}px)  rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;

          $slideEl.transform(slideTransform);
          $slideEl[0].style.zIndex = -Math.abs(Math.round(offsetMultiplier)) + 1;
          // if (params.slideShadows) {
          //   // Set shadows
          //   let $shadowBeforeEl = isHorizontal ? $slideEl.find('.swiper-slide-shadow-left') : $slideEl.find('.swiper-slide-shadow-top');
          //   let $shadowAfterEl = isHorizontal ? $slideEl.find('.swiper-slide-shadow-right') : $slideEl.find('.swiper-slide-shadow-bottom');
          //   if ($shadowBeforeEl.length === 0) {
          //     $shadowBeforeEl = swiper.$(`<div class="swiper-slide-shadow-${isHorizontal ? 'left' : 'top'}"></div>`);
          //     $slideEl.append($shadowBeforeEl);
          //   }
          //   if ($shadowAfterEl.length === 0) {
          //     $shadowAfterEl = swiper.$(`<div class="swiper-slide-shadow-${isHorizontal ? 'right' : 'bottom'}"></div>`);
          //     $slideEl.append($shadowAfterEl);
          //   }
          //   if ($shadowBeforeEl.length) $shadowBeforeEl[0].style.opacity = offsetMultiplier > 0 ? offsetMultiplier : 0;
          //   if ($shadowAfterEl.length) $shadowAfterEl[0].style.opacity = (-offsetMultiplier) > 0 ? -offsetMultiplier : 0;
          // }
        }

        // Set correct perspective for IE10
        if (swiper.support.pointerEvents || swiper.support.prefixedPointerEvents) {
          const ws = $wrapperEl[0].style;
          ws.perspectiveOrigin = `${center}px 50%`;
        }
      },
      setTransition(duration: number) {
        const swiper: any = this;
        swiper.slides
          .transition(duration)
          .find('.swiper-slide-shadow-top, .swiper-slide-shadow-right, .swiper-slide-shadow-bottom, .swiper-slide-shadow-left')
          .transition(duration);
      }
    }
  };

  return (
    <IonPage className="dashboard">
      <IonLabel class="version-label">{version}</IonLabel>
      <IonHeader className="dashboard-header">
        <IonToolbar className="dashboard-header-toolbar">
          <div slot="start" className="dashboard-header-toolbar-battery-icon">
            {getBatteryIcon()}
          </div>
          <IonImg className="dashboard-header-toolbar-logo" src={logoSvg} />
          <IonIcon slot="end" />
        </IonToolbar>
      </IonHeader>
      <IonContent className="dashboard-content">
        <IonList className="dashboard-content-list" lines="none">
          {/* <IonListHeader className="dashboard-content-list-header">
            <IonLabel>Temperature</IonLabel>
          </IonListHeader> */}
          <IonItem className="dashboard-content-list-item">
            <IonSlides options={slideOpts}
              pager={true} ref={slider} onIonSlideTransitionEnd={slideChanged}>
              {temperatures.map((temperature, index) => {
                return <IonSlide key={index} title={index.toString()}>
                  <div className="swiper-slide-content">
                    <svg viewBox="0 0 100 100">
                      <filter id="shadow">
                        <feDropShadow dx="4" dy="4" stdDeviation="2" floodColor="#989aa2" />
                      </filter>
                      <circle cx="50" cy="50" r="44" stroke="#fafafa" stroke-width="2" />
                      <text x="25" y="58" fill="#fafafa">
                        {temperature.toFixed(0)}&deg;C
                      </text>
                    </svg>
                  </div>
                  <div className="swiper-slide-footer">
                    <div className="swiper-slide-footer-text">
                      {slideLabels[index]}
                    </div>
                  </div>
                </IonSlide>;
              })}
            </IonSlides>
          </IonItem>
          <IonItem className="dashboard-content-list-footer" hidden={profile !== 4 && profile !== 5}>
            <IonRange min={100} max={220} step={1} value={profile > 0 && profile <= 6 && temperatures.length === 6 ? temperatures[profile - 1] : 0} snaps={true} color="danger" onIonChange={tempChange}>
              <IonIcon slot="start" size="small" color="danger" icon={thermometer} />
              <IonIcon slot="end" color="danger" icon={thermometer} />
            </IonRange>
          </IonItem>
          <IonItem className="dashboard-content-list-footer" hidden={profile === 4 || profile === 5}>
            &nbsp;
          </IonItem>
        </IonList>
      </IonContent>
      <IonAlert
        isOpen={tempDialogOpen}
        onDidDismiss={() => setTempDialogOpen(false)}
        header={'New Temperature'}
        inputs={[
          {
            name: 'temperature',
            type: 'number',
            label: 'Temperature',
            placeholder: '<New Value>',
            value: profile >= 1 && profile <= 6 ? temperatures[profile - 1] : 0,
            min: 100,
            max: 220,
          },
        ]}
        buttons={[
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
            handler: () => {
              console.log('Confirm Cancel');
            }
          },
          {
            text: 'Ok',
            handler: (values) => {
              const value = parseInt(values.temperature);
              if (value >= 100 && value <= 220) {
                updateTemperatureDispatch(value);
              }
            }
          }
        ]}
      />
      <IonAlert
        isOpen={props.displayLockDialog}
        onDidDismiss={() => props.dismissLockDialogCallback()}
        header={locked ? 'Unlock?' : 'Lock?'}
        message={`Would you like to ${locked ? "unlock" : "lock"} your Ghost MV1 vaporizer?`}
        buttons={[
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Ok',
            handler: (values) => {
              if (locked) {
                dispatch(unlock());
              } else {
                dispatch(lock());
              }
            }
          }
        ]}
      />
    </IonPage>
  );
};

export default Dashboard;
