import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { IBleState } from '../state/IBleState';
import { AppThunk } from '../store';
import { GhostBle } from '../../ble/GhostBle';

const initialState: IBleState = {
  connecting: false,
  connected: false,
  updating: false,
  connectAttempts: 0,
  locked: false,
  authKey: localStorage.getItem('ghostmv1.auth') || '',
  status: 0,
  battery: 0,
  profile: 0,
  temperatures: [170, 185, 200, 210, 220, 220],
};

const disconnect = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.disconnect();
  await dispatch(disconnected());
};

const connect = (allDevices: boolean = false): AppThunk => async (dispatch, getState) => {
  try {
    await GhostBle.connect(allDevices);
    dispatch(connectSuccess());
    setTimeout(() => {
      dispatch(startNotifications());
    }, 5000);
  } catch (e) {
    //dispatch(disconnected());
    return;
  }
};

const pair = (allDevices: boolean): AppThunk => async (dispatch, getState) => {
  try {
    await GhostBle.pair(allDevices);
    dispatch(connectSuccess());
    dispatch(startNotifications());
  } catch (e) {
    return;
  }
};

const updateCurrentProfile = (profile: number): AppThunk => async (dispatch, getState) => {
  try {
    if (getState().ble.locked) {
      return;
    }
    await dispatch(pauseNotifications());
    await dispatch(startUpdateProfile());
    const newProfile = await GhostBle.updateCurrentProfile(profile);
    if (newProfile) {
      await dispatch(setCurrentProfileForce(newProfile));
    }
  } finally {
    await dispatch(startNotifications());
    await dispatch(completeUpdateProfile());
  }
}

const updateTemperature = (values: number[], profile: number): AppThunk => async (dispatch, getState) => {
  try {
    if (getState().ble.locked) {
      return;
    }
    await dispatch(pauseNotifications());
    await dispatch(startUpdate());
    await GhostBle.updateTemperature(values, profile);
    await dispatch(setTemperatures(values));
  } finally {
    await dispatch(startNotifications());
    await dispatch(completeUpdate());
  }
}

const lock = (): AppThunk => async (dispatch, getState) => {
  try {
    await dispatch(pauseNotifications());
    await dispatch(requestLock());
    const success = await GhostBle.lock();
    if (success) {
      await dispatch(setStatus(getState().ble.status | 0x1 ))
    }
  } finally {
    await dispatch(completeLock());
    await dispatch(startNotifications());
    await dispatch(completeUpdate());
  }
}

const unlock = (): AppThunk => async (dispatch, getState) => {
  try {
    await dispatch(pauseNotifications());
    await dispatch(requestUnlock());
    const success = await GhostBle.unlock();
    if (success) {
      await dispatch(setStatus(getState().ble.status & 0xFE))
    }
  } finally {
    await dispatch(completeUnlock());
    await dispatch(startNotifications());
    await dispatch(completeUpdate());
  }
}

const startNotifications = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.startNotifications();
  await new Promise(resolve => setTimeout(() => resolve(), 250));
}

const pauseNotifications = (): AppThunk => async (dispatch, getState) => {
  await GhostBle.pauseNotifications();
  await new Promise(resolve => setTimeout(() => resolve(), 250));
}

const bleSlice = createSlice({
  name: 'BLE',
  initialState,
  reducers: {
    startUpdate:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: true,
    }),
    completeUpdate:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: false,
    }),
    startUpdateProfile:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: true,
    }),
    completeUpdateProfile:  (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: false,
    }),
    connectStart: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: true,
      connected: false,
      error: undefined,
      connectAttempts: state.connectAttempts+1,
    }),
    connectSuccess: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: false,
      connected: true,
      error: undefined,
    }),
    connectFailed: (state: IBleState, action: PayloadAction<string>) => ({
      ...state,
      connecting: false,
      connected: false,
      error: action.payload,
    }),
    disconnected: (state: IBleState, action: PayloadAction) => ({
      ...state,
      connecting: false,
      connected: false,
      error: undefined,
      connectAttempts: 0,
    }),
    setAuthKey: (state: IBleState, action: PayloadAction<string>) => ({
      ...state,
      authKey: action.payload,
    }),
    setStatus: (state: IBleState, action: PayloadAction<number>) => ({
      ...state,
      status: action.payload,
      locked: (action.payload & 0x1) === 0x1,
    }),
    setBatteryLevel: (state: IBleState, action: PayloadAction<number>) => ({
      ...state,
      battery: action.payload,
    }),
    setCurrentProfileForce: (state: IBleState, action: PayloadAction<number>) => {
      return {
        ...state,
        profile: action.payload,
      };
    },
    setCurrentProfile: (state: IBleState, action: PayloadAction<number>) => {
      if (state.updating) {
        return state;
      } else {
        return {
          ...state,
          profile: action.payload,
        };
      }
    },
    setTemperatures: (state: IBleState, action: PayloadAction<number[]>) => ({
      ...state,
      temperatures: action.payload
    }),
    clearError: (state: IBleState, action: PayloadAction) => ({
      ...state,
      error: undefined,
    }),
    requestLock: (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: true,
      locking: true,
    }),
    requestUnlock: (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: true,
      locking: true,
    }),
    completeLock: (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: false,
      locking: false,
      locked: true,
    }), 
    completeUnlock: (state: IBleState, action: PayloadAction) => ({
      ...state,
      updating: false,
      locking: false,
      locked: false,
    }),
  }
});

export { 
  connect, disconnect, pair, 
  updateCurrentProfile, updateTemperature, 
  startNotifications, pauseNotifications, 
  lock, unlock, 
};

export const { 
  connectStart, connectSuccess, connectFailed, 
  disconnected, 
  setAuthKey,
  setStatus,
  setBatteryLevel, 
  setCurrentProfile,
  setCurrentProfileForce,
  setTemperatures,
  clearError,
  startUpdate,
  completeUpdate,
  startUpdateProfile,
  completeUpdateProfile,
  requestLock,
  requestUnlock,
  completeLock,
  completeUnlock,
} = bleSlice.actions;
export default bleSlice.reducer